class Instruccionclass {
    constructor(id, tipo, n, nombre){
      this.id = id;     //Identificador nico de la instruccin
      this.tipo = tipo; //Tipo de instruccin
      this.n = n;       // Parmetro de la instruccin
      this.nombre = nombre;
    }
    mostrarNombre(){
      return this.tipo
    }
  }

  export default Instruccionclass;